------------------\r\n * Put style rules here that you want to apply globally. These styles are for\r\n * the entire app and not just one component. Additionally, this file can be\r\n * used as an entry point to import other CSS/Sass files to be included in the\r\n * output CSS.\r\n * For more information on global stylesheets, visit the documentation:\r\n * https://ionicframework.com/docs/layout/global-stylesheets\r\n */\r\n\r\n/* Core CSS required for Ionic components to work properly */\r\n@import \"~@ionic/angular/css/core.css\";\r\n\r\n/* Basic CSS for apps built with Ionic */\r\n@import \"~@ionic/angular/css/normalize.css\";\r\n@import \"~@ionic/angular/css/structure.css\";\r\n@import \"~@ionic/angular/css/typography.css\";\r\n@import '~@ionic/angular/css/display.css';\r\n\r\n/* Optional CSS utils that can be commented out */\r\n@import \"~@ionic/angular/css/padding.css\";\r\n@import \"~@ionic/angular/css/float-elements.css\";\r\n@import \"~@ionic/angular/css/text-alignment.css\";\r\n@import \"~@ionic/angular/css/text-transformation.css\";\r\n@import \"~@ionic/angular/css/flex-utils.css\";\r\n\r\n.m-icon {\r\n    margin-right: 3px;\r\n}\r\n\r\n.alert-wrapper.sc-ion-alert-md {\r\n    width: -webkit-fill-available;\r\n}\r\n\r\n.font-price {\r\n    font-size: 2.5rem;\r\n    color: firebrick;\r\n    font-weight: bold;\r\n    line-height: 1.15;\r\n    margin: 20px 20px 10px 0px;\r\n}\r\n\r\n.float-right {\r\n    float: right !important;\r\n    text-align: end;\r\n}\r\n\r\nion-card{\r\n    padding: 10px 0px 10px 0px;\r\n}\r\n\r\n.input-group-quantity {\r\n    position: relative;\r\n    display: flex;\r\n    height: min-content;\r\n    width: min-content;\r\n    flex-wrap: wrap;\r\n    align-items: stretch;\r\n}\r\n\r\n.input-prepend {\r\n    display: flex;\r\n    align-items: center;\r\n    background-color: #ced4da;\r\n    padding: 10px;\r\n    border-radius: 3px;\r\n    cursor: pointer;\r\n}\r\n\r\n.input-quantity {\r\n    text-align: center;\r\n    width: min-content;\r\n    width: 38px;\r\n    text-align: initial;\r\n}\r\n.btn-group, .btn-group-vertical {\r\n    border: 1px solid #cccccc;\r\n    border-radius: 5px;\r\n}\r\n\r\n.car-subtitle {\r\n    width: 80%;\r\n    margin-left: 5px;\r\n    margin-top: -16px;\r\n}\r\n\r\n.btn-spa {\r\n    margin-left: 5px;\r\n}\r\n\r\n.line-card{\r\n    --border-color: transparent;\r\n}\r\n\r\nion-select {\r\n    /* Applies to the value and placeholder color */\r\n    color: #000;\r\n  \r\n    /* Set a different placeholder color */\r\n    --placeholder-color: #000;\r\n  \r\n    /* Set full opacity on the placeholder */\r\n    --placeholder-opacity: 1;\r\n  }\r\n\r\n@import \"~@mobiscroll/angular/dist/css/mobiscroll.scss\";","/*\r\n * App Global CSS\r\n * ----------------------------------------------------------------------------\r\n * Put style rules here that you want to apply globally. These styles are for\r\n * the entire app and not just one component. Additionally, this file can be\r\n * used as an entry point to import other CSS/Sass files to be included in the\r\n * output CSS.\r\n * For more information on global stylesheets, visit the documentation:\r\n * https://ionicframework.com/docs/layout/global-stylesheets\r\n */\n/* Core CSS required for Ionic components to work properly */\n@import url(~@ionic/angular/css/core.css);\n/* Basic CSS for apps built with Ionic */\n@import url(~@ionic/angular/css/normalize.css);\n@import url(~@ionic/angular/css/structure.css);\n@import url(~@ionic/angular/css/typography.css);\n@import url(~@ionic/angular/css/display.css);\n/* Optional CSS utils that can be commented out */\n@import url(~@ionic/angular/css/padding.css);\n@import url(~@ionic/angular/css/float-elements.css);\n@import url(~@ionic/angular/css/text-alignment.css);\n@import url(~@ionic/angular/css/text-transformation.css);\n@import url(~@ionic/angular/css/flex-utils.css);\n.m-icon {\n  margin-right: 3px;\n}\n\n.alert-wrapper.sc-ion-alert-md {\n  width: -webkit-fill-available;\n}\n\